@import '_config';

@import '../components/button/button.mixins';
@import '../components/charts/simple-bar/simple-bar-chart.mixins';

.vanilla {
	background: $color--light-grayish-yellow;

	@include rc-button(
		$font-color: $color--vanilla,
		$background-color: $color--soft-blue
	)

	@include rc-simple-bar-chart(
		$font-color: $color--vanilla,
		$bar-color: $color--soft-blue
	)
}



